* {
  box-sizing: border-box;
}

html {
  background: white;
}

button {
  appearance: none;
  cursor: pointer;
  background: none;
  font: inherit;
  padding: 0px;
  border: none;
  line-height: 1;
}

button:focus {
  outline: none;
}

.item-bar {
  display: grid;
  grid-template-columns: 75% 25%;
  background: #747d8c;
  border-bottom: 1px solid white;
}

.bar-left {
  padding: 20px;
  cursor: pointer;
}

.bar-left h2 {
  margin: 0;
  padding: 0;
  line-height: 1;
  font-weight: bold;
  color: white;
}

.bar-right {
  grid-column-start: 2;
  grid-column-end: 2;
}

.bar-btns {
  display: grid;
  grid-template-columns: auto auto auto;
  height: 100%;
}

.bar-btns > button {
  width: 100%;
  background: #57606f;
  transition: all 0.3s;
  color: white;
}

.edit-toggle-btn.editing-true {
  background: #2ed573;
}

.bar-btns > button:hover {
  background: #2ed573;
}

.form-btn {
  padding-right: 20px;
  margin: 5px;
  background: #17c0eb;
  text-transform: uppercase;
  border-radius: 100px;
  color: white;
  transition: all 0.3s;
}

.form-btn:hover {
  background: #18dcff;
}

.form-btn:hover .btn-icon {
  background: #17c0eb;
  transform: scale(1.1);
}

.form-btn .btn-icon {
  display: inline-block;
  vertical-align: middle;
  padding: 18px 20px;
  background: #18dcff;
  border-radius: 100px;
  transition: all 0.3s;
}

.form-btn > span {
  padding-left: 15px;
  font-size: 12px;
  font-weight: bold;
  vertical-align: middle;
}

.form-btn.delete-btn {
  background: #ff4d4d;
}

.form-btn.delete-btn .btn-icon {
  background: #ff3838;
}

.form-btn.delete-btn:hover {
  background: #ff3838;
}

.form-btn.delete-btn:hover .btn-icon {
  background: #ff4d4d;
}

.form-btn.update-btn {
  background: #7d5fff;
}

.form-btn.update-btn .btn-icon {
  background: #7158e2;
}

.form-btn.update-btn:hover {
  background: #7158e2;
}

.form-btn.update-btn:hover .btn-icon {
  background: #7d5fff;
}

.section-list,
.part-list,
.conditions-list {
  padding: 10px 20px;
  background-color: white;
}

.part-list {
  background-color: #dfe4ea;
}

.conditions-list {
  background: #f1f2f6;
}
.form-edit {
  background: #f1f2f6;
  padding: 20px;
  text-align: center;
}

.form-input-container {
  max-width: 600px;
  margin: 10px auto;
  display: grid;
  grid-template-columns: 50% 50%;
}

.form-input {
  display: inline-block;
  text-align: left;
  padding: 10px;
}

.form-input label,
.form-input input {
  display: block;
}

.form-input label {
  font-weight: bold;
  text-transform: uppercase;
  font-size: 10px;
}

.form-input input.form-input-field {
  background: #eaeaea;
  border: none;
  border-radius: 0px;
  width: 100%;
  margin: 0;
  box-shadow: none;
  padding: 5px 15px;
}

.form-input select {
  background-color: #eaeaea;
  border: none;
  border-radius: 0px;
  width: 100%;
  margin: 0;
  -webkit-box-shadow: none;
  box-shadow: none;
  padding: 5px 24px 5px 15px;
}

.form-input label > span,
.form-input label > .help-tooltip {
  vertical-align: middle;
}

.help-tooltip {
  position: relative;
  display: inline-block;
  padding-left: 5px;
}

.help-tooltip .help-tooltip-content {
  position: absolute;
  display: none;
  width: 200px;
  background: white;
  z-index: 2;
  padding: 5px 10px;
  text-transform: none;
  font-weight: normal;
  top: 0;
  left: 20px;
}

.help-tooltip:hover .help-tooltip-content {
  display: block;
}

.condition select {
  max-width: 150px;
}

.condition {
  padding: 10px 0;
  display: grid;
  grid-template-columns: auto auto auto auto 25%;
  align-items: center;
}

.conditions-btns {
  text-align: right;
}

.icon-btn {
  background: #17c0eb;
  text-transform: uppercase;
  color: white;
  transition: all 0.3s;
}

.icon-btn:hover {
  background: #18dcff;
}

.icon-btn:hover .btn-icon {
  background: #17c0eb;
}

.icon-btn .btn-icon {
  display: inline-block;
  vertical-align: middle;
  padding: 13px 15px;
  background: #18dcff;
  transition: all 0.3s;
}

.icon-btn.delete-btn {
  background: #ff4d4d;
}

.icon-btn.delete-btn .btn-icon {
  background: #ff3838;
}

.icon-btn.delete-btn:hover {
  background: #ff3838;
}

.icon-btn.delete-btn:hover .btn-icon {
  background: #ff4d4d;
}

.icon-btn.update-btn {
  background: #7d5fff;
}

.icon-btn.update-btn .btn-icon {
  background: #7158e2;
}

.icon-btn.update-btn:hover {
  background: #7158e2;
}

.icon-btn.update-btn:hover .btn-icon {
  background: #7d5fff;
}
